






























































import Vue from "vue";
import { Component } from "vue-property-decorator";
import Layout from "@/components/Layout.vue";
import Icon from "@/components/Icon.vue";
import dayjs from "dayjs";
import clone from "@/lib/clone";
import Blank from "@/components/Blank.vue";

type Group = {
  name: string;
  items: RecordItem[];
};

@Component({
  components: { Icon, Layout, Blank },
})
export default class Bill extends Vue {
  // logo: string = logo;
  // 
  //   
  month = (dayjs().month() + 1).toString();
  year = dayjs().year().toString();

  get years() {
    const endYear = dayjs().year();
    let y = 1970;
    const result: number[] = [];
    while (y <= endYear) {
      result.push(y);
      y++;
    }
    return result;
  }
  //   localSstore 
  get recordList() {
    return this.$store.state.recordList;
  }
  //  
  get xx() {
    let total = 0;
    //   total
    return total;
  }
  beautifyMonth(m: number) {
    return m < 10 ? "0" + m.toString() : m.toString();
  }
  get list() {
    const results: Group[] = [];
    const names: string[] = [];
    // 
    const list = clone<RecordItem[]>(this.recordList)
      .filter(
        (item) =>
          dayjs(item.createdAt).year() === parseInt(this.year) &&
          dayjs(item.createdAt).month() + 1 === parseInt(this.month)
      )
      .sort((b, a) => {
        return dayjs(a.createdAt).valueOf() - dayjs(b.createdAt).valueOf();
      });
    //   
    let record: RecordItem;
    for (record of list) {
      const date = dayjs(record.createdAt).toISOString().split("T")[0]; // 2021-2-2
      const index = names.indexOf(date);
      if (index < 0) {
        names.push(date);
        results.push({ name: date, items: [record] });
      } else {
        // 
        results[index].items.push(record);
      }
    }
    return results;
  }
  //  eg 
  getTitle(group: Group) {
    const day = dayjs(group.name);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return ` ${this.toWeekday(day.day())}`;
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return ` ${this.toWeekday(day.day())}`;
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return ` ${this.toWeekday(day.day())}`;
    } else {
      return `${day.format("Md")} ${this.toWeekday(day.day())}`;
    }
  }
  // 
  getTotal(group: Group) {
    let total = 0;
    let item: RecordItem;
    for (item of group.items) {
      if (item.type === "-") {
        total -= item.amount;
      } else if (item.type === "+") {
        total += item.amount;
      }
    }
    if (total <= 0) {
      return `: ${Math.abs(total)}`;
    } else {
      return `: ${Math.abs(total)}`;
    }
  }
  //   -   
  getAmount(record: RecordItem) {
    if (record.type === "+") {
      return record.amount;
    } else {
      return -record.amount;
    }
  }
  // 
  get totalIncome() {
    let total = 0;
    let group: Group;
    for (group of this.list) {
      let record: RecordItem;
      for (record of group.items) {
        if (record.type === "+") {
          total += record.amount;
        } else {
          continue;
        }
      }
    }
    return total;
  }
  // 
  get totalExpense() {
    let total = 0;
    let group: Group;
    for (group of this.list) {
      let record: RecordItem;
      for (record of group.items) {
        if (record.type === "-") {
          total += record.amount;
        } else {
          continue;
        }
      }
    }
    return total;
  }
  //   
  toWeekday(value: number) {
    if (value >= 0 && value <= 6) {
      return [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ][value];
    }
  }
}
